@charset "UTF-8";
/* ==========================
   Global Styles
========================== */
.main {
  flex: 1;
  background-color: #12002b;
  /* Fond sombre violet */
}

.bg-dark {
  background-color: #12002b;
}

/* ==========================
   Header Styles
========================== */
.header {
  width: 100%;
  background-color: #12002b;
  color: #fff;
  padding: 40px 0;
  /* Espacement plus large pour centrer */
  margin-bottom: 2rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  /* Dispose le titre et le bouton en colonne */
  justify-content: center;
  /* Centre verticalement */
  align-items: center;
  /* Centre horizontalement */
}

h1 {
  font-size: 32px;
  /* Ajuster la taille du titre */
  margin: 0;
}

/* ==========================
   Edit Button Styles
========================== */
.edit-button {
  border-color: #00bc77;
  background-color: #00bc77;
  color: #fff;
  font-weight: bold;
  padding: 10px;
  margin-top: 1rem;
  width: 200px;
  /* Largeur du bouton */
  display: block;
  margin: 20px auto;
  /* Centrer le bouton horizontalement */
  text-align: center;
  /* Centrer le texte dans le bouton */
}

/* ==========================
   Transactions Styles
========================== */
.transactions {
  display: flex;
  flex-direction: column;
  /* Dispose les transactions en colonne */
  align-items: center;
  /* Centrer les éléments des transactions */
  margin: 0 auto;
  width: 80%;
  /* Ajuster la largeur pour que ça ne soit pas trop large */
}

.account {
  display: flex;
  flex-direction: column;
  /* Dispose les éléments en colonne pour chaque compte */
  align-items: center;
  /* Centrer les éléments à l'intérieur du compte */
  background-color: #fff;
  padding: 1.5rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  text-align: left;
}

.account-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 0.5rem;
}

.account-amount {
  font-size: 2rem;
  font-weight: bold;
  color: #00bc77;
  margin-bottom: 0.25rem;
}

.account-amount-description {
  font-size: 1rem;
  color: #555;
}

/* ==========================
   Button Styles for Transactions
========================== */
.transaction-button {
  padding: 10px 20px;
  background-color: #00bc77;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 1rem;
}
.transaction-button:hover {
  background-color: #008957;
}

/* ==========================
   Media Queries (for responsiveness)
========================== */
@media (min-width: 720px) {
  .transactions {
    width: 60%;
    /* Ajustement de la largeur sur grand écran */
  }
  .account {
    flex-direction: row;
    /* Les éléments du compte seront en ligne sur grand écran */
  }
  .transaction-button {
    width: 200px;
    /* Ajuster la largeur du bouton sur grand écran */
  }
}